import React from 'react';

import { useStaticQuery, graphql } from 'gatsby';

import SEO from '../components/SEO/seo';
import Layout from '../components/layout/layout';
import { HomepageContainer } from '../page-styles/index.styles';
import Bio from '../components/bio/bio.component';
import Projects from '../components/projects/projects.component';
import BackToTop from '../components/back-top-top/back-to-top.component';

const IndexPage = () => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  );

  return (
    <Layout>
      <SEO title={`${site.siteMetadata?.title} - Software Engineer`} />
      <HomepageContainer>
        <Bio />
        <Projects />
        <BackToTop />
      </HomepageContainer>
    </Layout>
  );
}

export default IndexPage;
